<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">推荐码</span>
      </div>
      <div class="content-wrapper">
          <div class="content" ref="content">
            <div class="top">
              <div class="icon">
                <img :src="logoUrl" alt="">
              </div>
              <span class="text">{{name}}</span>
            </div>
            <div class="qrcode-wrapper">
              <img :src="qrcode" alt="">
            </div>
            <div class="bottom">
              我的推广二维码
            </div>
          </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import {getCurrentMerchant,getCurrentUser} from 'api/mine'
  import {ERR_CODE,ORIGIN} from 'api/config'

  export default {
    data () {
      return {
        name: '',
        logoUrl: '',
        qrcode: ''
      }
    },
    created () {
      this._getCurrentMerchant()
      this._getCurrentUser()
      window.resize = () => {
        this._calcWidth()
      }
    },
    mounted () {
      this._calcWidth()
    },
    methods: {
      back() {
        this.$router.back()
      },
      _calcWidth() {
        let clientWidth = window.innerWidth
        let content = this.$refs.content
        let width
        if(clientWidth < 340) {
          width = 280
        } else {
          width = 320
        }
        content.style.width = `${width}px`
      },
      _getCurrentMerchant(){
        getCurrentMerchant().then((res) => {
          if(res.errCode === ERR_CODE){
            this.name = res.data.name
            this.logoUrl = res.data.logoUrl
          }
        })
      },
      _getCurrentUser(){
        getCurrentUser().then((res) => {
          if(res.errCode === ERR_CODE){
            this.qrcode = ORIGIN + res.data.introQRCoderUrl
          }
        })
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    background $color-background
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      display flex
      justify-content center
      align-items center
      .content
        width 320px
        height 433px
        background #fff
        display flex
        flex-direction column
        .top
          margin 30px
          height 60px
          display flex
          align-items center
          .icon
            width 60px
            height 60px
            img
              display block
              width 100%
              height 100%
          .text
            margin-left 24px
            color $color-header-text
            font-size $font-size-large
        .qrcode-wrapper
          height 240px
          width 240px
          margin-left auto
          margin-right auto
          img
            display block
            width 100%
            height 100%
        .bottom
          margin-top 20px
          font-size $font-size-medium
          color #808080
          height 24px
          display flex
          align-items center
          justify-content center
</style>



